
import Events from './Events';
import { EVENTS } from '../config';

export default class MessageBus {
    constructor(target) {
        this.target = target;
		this.events = new Events();
        this.listenForMessages();
    }

    listenForMessages(){
        window.addEventListener('message', this.onMessageReceived.bind(this), false);
    }

    sendMessage(obj) {
        // console.log('SEND MESSAGE TO PARENT', obj, JSON.stringify(obj));
        const message = JSON.stringify(obj);
        this.target.postMessage(message, '*');
    }

    onMessageReceived(evt) {
        if(evt.data.source === 'react-devtools-detector')
            return false;

        const messageData = JSON.parse(evt.data);
        // console.log('CHILD got a message:', messageData);

        const action = messageData.action;
        if (action) {
            switch (action) {
                case 'start':
                    this.events.emitEvent(EVENTS.start, {
                        parentURL: messageData.parentURL
                    });
                    break;
                case 'route':
                    // this.events.emitEvent(EVENTS.route, {
                    //     route: messageData.route
                    // })
                    break;

                default:
                    break;
            }
        }
    }
}