import React from 'react';

export default class MetaGenerator {
    constructor(obj) {
        this.canonicalURL = obj.canonicalURL;
        this.name = obj.name;
        this.prefix = this.key;
        this.description = obj.description;
    }

    generateMetatags() {
        const tags = [];
        tags.push(<meta key={`${this.prefix}_charset`} charSet="utf-8" />);
        tags.push(<title key={`${this.prefix}_title`}>{this.name}</title>);
        tags.push(<meta key={`${this.prefix}_description`}name="description" content={this.description} />);

        if(this.canonicalURL && this.canonicalURL!== '') {
            tags.push(<link key={`${this.prefix}_canonical`} rel="canonical" href={this.canonicalURL} />);
        }

        return tags;
    }
}