import React, { Component } from 'react'
import { Route } from "react-router-dom";

import Footer from './components/footer/Footer';
import About from './components/about/About';
import Chilies from './components/chilies/Chilies';
import Home from './components/home/Home';
import ScovilleScale from './components/scovillescale/ScovilleScale';

export default class Main extends Component {
  render() {
    const {parentURL} =  this.props;
    return (
        <div className="App">
            <main className="App-main">
                <Route exact path={`${ process.env.PUBLIC_URL }/`} render={(props) => <Home {...props} parentURL={parentURL} name='Chilidex | A√∫me' />} />
                <Route path={`${ process.env.PUBLIC_URL }/about`} render={(props) => <About {...props} parentURL={parentURL} name='Chilidex | Abaute' />}  />
                <Route path={`${ process.env.PUBLIC_URL }/chilies`} render={(props) => <Chilies {...props} parentURL={parentURL} name='Chilidex | Xiliche' />}  />
                <Route path={`${ process.env.PUBLIC_URL }/scovillescale`} render={(props) => <ScovilleScale {...props} parentURL={parentURL} name='Chilidex | Scovillilas squeille' />}  />
            </main>
            <Footer />
        </div>
    )
  }
}
