const events = {};
const hasProp = events.hasOwnProperty;

/**
 * Events class to handle event listening and emiting
 */
export default class EventsManager {
  addListener(evt, listener) {
    // Create the event's object if not yet created
    if (!hasProp.call(events, evt)) events[evt] = [];

    // Add the listener to queue
    const index = events[evt].push(listener) - 1;

    // Provide handle back for removal of event
    return {
      remove: function () {
        delete events[evt][index];
      }
    };
  }

  emitEvent(evt, info) {
    // If the event doesn't exist, or there's no listeners in queue, just leave
    if (!hasProp.call(events, evt)) return;

    // Cycle through events queue, fire!
    events[evt].forEach(function (item) {
      item(info !== undefined ? info : {});
    });
  }
}
