import React, { Component } from 'react'
import Helmet from 'react-helmet'
import Metagenerator from '../../utils/MetaGenerator'

export default class Chilies extends Component {
  constructor() {
    super();

    this.chiliesList = [
      {
        'name': 'Fiery Gut of Doom',
        'heat': 'Nuclear'
      },
      {
        'name': 'Molten Lava',
        'heat': 'Nuclear'
      },
      {
        'name': 'Face Melter',
        'heat': 'Nuclear'
      },
      {
        'name': 'Manly Pepper',
        'heat': 'Very Hot'
      },
      {
        'name': 'Fairy spice',
        'heat': 'Mild'
      },
    ];
  }

  updateMetatags(){
    const { parentURL, match, name } = this.props;
    let canonicalURL = (parentURL && parentURL !== '') ? `${parentURL+match.path}`: '';
    const
      metatagsData = {
        name: name,
        canonicalURL: canonicalURL ? canonicalURL : '',
        prefix: 'chilies',
        description: 'A bucket full of made up chili species',
      },
      metagenerator = new Metagenerator(metatagsData);

    this.metatags = metagenerator.generateMetatags();
  }

  generateChilisList(){
    const arr = [];
    for (var i = 0; i < this.chiliesList.length; i++) {
      const chili = this.chiliesList[i];
      arr.push(<li key={i}>{chili.name} - {chili.heat}</li>);
    }
    return arr;
  }

  render() {
    const chilis = this.generateChilisList();
    this.updateMetatags();

    return (
      <section className="section">
        <Helmet>
          {this.metatags}
        </Helmet>
        <header className="section__header">
          <h2 className="section__title">Chili Peppers</h2>
        </header>
        <div className="section__content">
          <ul className="section__list">
            {chilis}
          </ul>
        </div>
      </section>
    )
  }
}
