import { Component } from 'react'
import PropTypes from 'prop-types';
import { EVENTS, ACTIONS } from '../config';


export default class LocationListener extends Component {
    static contextTypes = {
        router: PropTypes.object
    };

    constructor() {
        super();
        this.isFirstLoad = true;
    }

    componentDidMount() {
        // console.log('@ LocationListener: componentDidMount');
        const { events } = this.props;
        this.events = events;

        this.handleLocationChange(this.context.router.history.location);
        this.unlisten = this.context.router.history.listen( this.handleLocationChange.bind(this) );
    }

    componentWillUnmount() {
        this.unlisten();
    }

    handleLocationChange(location) {
        // console.log(`- - - location: '${location.pathname}' ${document.title}`);
        if(!this.isFirstLoad && location.pathname!==this.lastLocation) {
            this.events.emitEvent(EVENTS.route, {
                action: ACTIONS.route,
                route: location.pathname,
            });
        } else {
            this.isFirstLoad = false;
        }

        this.lastLocation = location.pathname;

    }

    render() {
        return this.props.children;
    }
}