import React, { Component } from 'react';
import { BrowserRouter as Router } from "react-router-dom";
import MessageBus from './utils/MessageBus';
import Events from './utils/Events';
import LocationListener from './utils/LocationListener';
import { EVENTS, ACTIONS } from './config';

import Main from './Main';
import './App.css';

class App extends Component {

  constructor() {
    super();
    // this might not be the 'react way' of doing things, but bear with me...it's been a while ..
    this.events = new Events();
    this.messageBus = new MessageBus(window.parent);
    this.addListeners();

    this.state = {
      parentURL: ''
    };
  }

  addListeners() {
    this.events.addListener(EVENTS.start, this.onStart.bind(this));
    this.events.addListener(EVENTS.route, this.onRouteChanged.bind(this));
  }

  onRouteChanged(evt) {
    this.messageBus.sendMessage(evt, '*');
  }

  onStart(evt) {
    const height = 500;
    this.messageBus.sendMessage({'action': ACTIONS.height, 'height': height});

    this.setState({
      parentURL: evt.parentURL
    });
  }

  render() {
    const {parentURL} =  this.state;
    return (
      <Router>
        <LocationListener events={this.events}>
          <Main parentURL={parentURL} />
        </LocationListener>
      </Router>
    );
  }
}

export default App;
